.App {
  text-align: center;
  background-color: #062f4f;
}
img {
  height: 40vmin;
  pointer-events: none;
}
.navbar {
  flex-direction: column;
  display: flex;
  color: white;
  background-color: black;
  font-size: x-large;
  align-items: center;
}

.intro {
  display: flex;
  align-items: baseline;
  font-size: xx-large;
  color: white;
  border: 2px solid green;
  /* background-color: #813772;     */
  padding: 10px;
  margin: 0.5rem;
  height: 60vh;
}

.navbar h1 {
  display: flex;
  flex: 5;
  margin: 0.75rem;
}

.navbar h4 {
  padding: 0;

  margin: 1rem;
  flex: 2;
  align-items: baseline;
  max-height: 5rem;
}

.ProjectCard {
  /* border: 3px solid orange; */
  margin: 10px;
  display: flex;
  flex-direction: column;
}
/* ----LEFT--------- */
.ProjectCard .left {
  justify-content: space-around;
  /* border: 2px solid red; */
}
.ProjectCard .left img {
  width: 45%;
  height: 45%;
}
/*------RIGHT---------*/
.ProjectCard .right {
  display: flex;
  flex-direction: column;
  /* border: 2px solid rgb(182, 182, 255); */
  font-size: 2rem;
}
.ProjectCard .right a {
  /* margin-left: 0.5rem;
  margin-right: 0.5rem; */
  margin: 0.5rem;
  max-height: 5rem;
  border-radius: 8px;
  padding: 2px;

  color: #062f4f;
  background-color: #813772;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;

  justify-content: space-around;
}
.ProjectCard .right img {
  text-decoration: none;
  width: 25%;
  height: 25%;
  /* border: 2px solid teal; */
}

/* --------------------------- */
.hide {
  filter: blur(5px);
  opacity: 0;
  transform: translateX(-20%);
}

.show {
  filter: blur(0px);
  opacity: 1;
  transform: translateX(0);
  transition: all 1s;
}

@media (min-width: 800px) {
  .intro {
    height: 80vh;
  }
  .navbar {
    justify-content: space-around;
    flex-direction: row;
  }
  .navbar h1 {
    flex: 4;
    margin-left: 1rem;
  }

  .navbar h4 {
    flex: 1;
  }

  .ProjectCard {
    flex-direction: row;
    /* justify-content: space-around; */
  }
  .ProjectCard .left {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    border-width: 6px;
    flex: 1;
  }
  .ProjectCard .left img {
    width: 45%;
    height: auto;
  }
  .ProjectCard .right {
    border-width: 6px;
    flex: 2;
  }
  .ProjectCard .right img {
    width: 25%;
    height: auto;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
